* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 30px;
}

.inputBox {
  position: relative;
  width: 250px;
}

.inputBox input{
  width: 100%;
  padding: 10px;
  border: 1 px solid rgba(255, 255, 255, 0.25);
  border-radius: 5px;
  outline: none;
  color: black;
  font-size: 1em;
  transition: 0.5s;
}

.inputBox select{
  width: 100%;
  padding: 10px;
  border: 1 px solid rgba(255, 255, 255, 0.25);
  border-radius: 5px;
  outline: none;
  color: black;
  font-size: 1em;
  transition: 0.5s;
}

.inputBox span {
  position: absolute;
  left: 0;
  padding: 10px;
  pointer-events: none;
  font-size: 1em;
  color: rgba(165, 165, 165, 0.849);

  transition: 0.5s;
}

.spanCheckbox{
  margin-left: 1em;
}


.inputBox input:valid ~ span,
.inputBox input:focus ~ span {
  color: #31acdd;
  transform: translateX(10px) translateY(-7px);
  font-size: 0.8em;
  padding: 0 10px;
  background: #fff;
  letter-spacing: 0.1em;
}

.inputBox select:valid ~ span,
.inputBox select:focus ~ span {
  color: #31acdd;
  transform: translateX(10px) translateY(-7px);
  font-size: 0.8em;
  padding: 0 10px;
  background: #fff;
  letter-spacing: 0.1em;
}

.inputBox input:valid,
.inputBox input:focus{
  border: 1 px solid #00dfc4;

}

.table-fill{
  background: white;
  border-radius: 6px;
  border-collapse: collapse;
  height: 320px;
 
  max-width: 600px;
  padding: 5px;
  width:500px;
  box-shadow: 0 5px 10px rgba(0,0,0,0.1);
  animation: float 5s infinite;
}

th{
  color:#000000;
  background-color: #03b8ff9a;
  border-bottom: 4px solid rgb(252, 252, 252);
  border-right: 1px solid rgb(255, 255, 255);
  font-size: 20px;
  font-weight: 20;
  padding: 10px;
  text-align: left;
  text-shadow: 0 1px rgba(0,0,0,0.1);
  vertical-align: middle;
}

th:first-child{
  border-top-left-radius: 3px;
}

th:last-child{
  border-top-right-radius: 3px;
  border-right: none;
}

tr{
  border-top: 1px solid #C1c3d1;
  border-bottom: 1px solid #C1c3d1;
  color: rgb(0, 0, 0);
  font-size: 16px;
  font-weight: normal;
  text-shadow: 0 1px 1px rgb(256, 256, 256,0.1);
}

tr:hover td{
  background-color: #f3f0f0;
  color: #000000;
  border-top: 1px solid #22262e;
}

tr:first-child{
  border-top: none;
}

tr:last-child{
  border-bottom: none;
}

tr:last-child td:first-child{
  border-bottom-left-radius: 3px;
}

tr:last-child td:last-child{
  border-bottom-right-radius: 3px;
}

td{
  background: #fff;
  padding: 10px;
  text-align: left;
  vertical-align: middle;
  font-weight: 100;
  font-size: 15px;
  text-shadow: -1px -1px 1px rgba(0,0,0,0.1);
  border-right: 1px solid #C1c3d1;
}

.container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;

}

.btn{
  width: 150px;
  height: 40px;
  border: none;
  color: #4e4e4e;
  border-radius: 4px;
  transition: ease-out 0.3s;
  font-size: 20px;
  outline: none;
  border: 3px solid #03b8ff9a;
  position: relative;
  z-index: 1;
}

.btn:hover {
  color: #4e4e4e;
  cursor: pointer;
}

